import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { UntypedFormGroup, FormControl, Validators, ValidationErrors } from '@angular/forms';
import { AuthService, User } from '@auth0/auth0-angular';
import { LocalStorageService } from '../../services/local-storage.service';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { ToastrService } from 'ngx-toastr';
import { UserData } from '../../data-models/user-models/user-data.model';
import { DOCUMENT } from '@angular/common';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  // check online status
  isOnline: boolean = false;
  onlineStatus: string = '';
  loginButton: string = '';

  // form validator error messages
  emailError: string = 'Required';

  loginForm: UntypedFormGroup = new UntypedFormGroup({
    email: new FormControl<string>('', [Validators.required, Validators.email])
  });

  constructor(
    private auth: AuthService,
    private router: Router,
    private onlineStatusService: OnlineStatusService,
    private toastr: ToastrService,
    @Inject(DOCUMENT) private document: Document
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.ONLINE) {
      this.isOnline = true;
      this.onlineStatus = 'online';
      this.loginButton = 'Login or Sign Up with Auth0';
    }
    else {
      this.isOnline = false;
      this.onlineStatus = 'offline';
      this.loginButton = 'Login Offline';
    }

  }

  ngOnInit(): void {

    // email validation
    this.loginForm.get('email')?.valueChanges.subscribe(() => {
      let emailErrors: ValidationErrors | null | undefined = this.loginForm.get('email')?.errors;
      if (emailErrors?.required) {
        this.emailError = 'Required';
      }
      else if (emailErrors?.email) {
        this.emailError = 'Invalid email format';
      }
      else {
        this.emailError = '';
      }
    });

  }

  // press the login button
  attemptLogin(): void {

    // attempt to login with the entered credentials
    // solution from https://community.auth0.com/t/isauthenticated-is-always-false/84794/3
    if (this.onlineStatusService.getStatus() === OnlineStatusType.ONLINE) {

      // if went offline while on page, reload app
      if (!this.isOnline) {
        window.location.reload();
        return;
      }

      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (!authd) {
          let query: string = window.location.search;
          let shouldParseResult: boolean = query.includes("code=") && query.includes("state=");
          if (shouldParseResult) {
            try {
              this.auth.handleRedirectCallback().subscribe((value) => {
                console.log("Logged in!", value);
              });
            } catch (err: unknown) {
              console.log("Error parsing redirect:", err);
            }
            window.history.replaceState({}, this.document.title, "/");
          }
          else {
            this.auth.loginWithPopup().subscribe((value: void) => {
              console.log("Logging in...", value);
            });
          }
        }
        else {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUserOnline(id, email).then(() => {
                  let error: string = LocalStorageService.error;
                  if (error && error !== 'not_found') {
                    this.toastr.error(error, "Error");
                    return;
                  }
                  let currUser: UserData = LocalStorageService.currUser;
                  if (!currUser || !currUser.userID || !currUser.userEmail) {
                    this.toastr.error("Please authenticate again", "User ID or Email not defined");
                    return;
                  }
                  let currHomepage: string = currUser.currHomepage;
                  this.router.navigate([currHomepage]);
                });
              }
              else {
                this.toastr.error("Please authenticate again", "User ID or Email not defined");
              }
            }
            else {
              this.toastr.error("User does not exist", "Something went wrong");
            }
          });
        }
      });
    }

    // offline login
    else {

      // if went online while on page, reload app
      if (this.isOnline) {
        window.location.reload();
        return;
      }

      if (this.loginForm.valid) {
        let username: string = this.loginForm.get('email')?.value;
        LocalStorageService.getUserOffline(username).then(() => {
          let error: string = LocalStorageService.error;
          if (error && error !== 'not_found') {
            this.toastr.error(error, "Error");
            return;
          }
          let currUser: UserData = LocalStorageService.currUser;
          if (!currUser || !currUser.userID || !currUser.userEmail) {
            this.toastr.error("User email not saved while online", "Invalid email");
            return;
          }
          let currHomepage: string = currUser.currHomepage;
          this.router.navigate([currHomepage]);
        });
      }
      else {
        this.toastr.error("User email required", "Invalid form");
      }
    }
  }

}
