import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService, User } from '@auth0/auth0-angular';
import { UserData } from '../../data-models/user-models/user-data.model';
import { ToastrService } from 'ngx-toastr';
import { OnlineStatusService, OnlineStatusType } from 'ngx-online-status';
import { AuthenticationService } from '../../services/authentication.service';
import { LocalStorageService } from '../../services/local-storage.service';

@Component({
  selector: 'app-offload-home',
  templateUrl: './offload-home.component.html',
  styleUrls: ['./offload-home.component.css']
})
export class OffloadHomeComponent {

  // data preambles
  requestToStartNewTrip: boolean = false; // flag that tells us if we should display the confirmation page
  userDetails: UserData = new UserData('', '');

  constructor(
    private auth: AuthService,
    private router: Router,
    private toastr: ToastrService,
    private onlineStatusService: OnlineStatusService,
    private authenticationService: AuthenticationService
  ) {

    if (this.onlineStatusService.getStatus() === OnlineStatusType.OFFLINE) {
      let currUsername: string = LocalStorageService.getCurrUsername();
      if (currUsername) {
        LocalStorageService.getUserOffline(currUsername).then(() => {
          let error: string = LocalStorageService.error;
          if (error) {
            this.authenticationService.userDataLogout(error);
          }
          let currUser: UserData = LocalStorageService.currUser;
          if (!currUser || !currUser.userID || !currUser.userEmail) {
            this.authenticationService.userDataLogout("User ID or Email not defined");
          }
          else {
            this.userDetails = currUser;
          }
        });
      }
      else {
        this.authenticationService.userDataLogout("Username not saved to storage");
      }
    }

    else {
      this.auth.isAuthenticated$.subscribe((authd: boolean) => {
        if (authd) {
          this.auth.user$.subscribe((user: User | null | undefined) => {
            if (user) {
              let id: string | undefined = user.sub;
              let email: string | undefined = user.email;
              if (id && email) {
                LocalStorageService.getUserOnline(id, email).then(() => {
                  let error: string = LocalStorageService.error;
                  if (error) {
                    this.authenticationService.userDataLogout(error);
                  }
                  let currUser: UserData = LocalStorageService.currUser;
                  if (!currUser || !currUser.userID || !currUser.userEmail) {
                    this.authenticationService.userDataLogout("User ID or Email is undefined");
                  }
                  else {
                    this.userDetails = currUser;
                  }
                });
              }
              else {
                this.authenticationService.userDataLogout("User ID or email is undefined");
              }
            }
            else {
              this.authenticationService.userDataLogout("Cannot get user data");
            }
          });
        }
        else {
          this.authenticationService.userDataLogout("Not authenticated");
        }
      });
    }

  }

  setNewTripFlag(): void {
    this.requestToStartNewTrip = true;
  }

  clearNewTripFlag(): void {
    this.requestToStartNewTrip = false;
  }

  // go to an offload report
  toOffloadReport(): void {
    this.router.navigate(['offload-report']);
  }

  // go to the new trip homepage
  toNoTripHome(): void {
    this.userDetails.currHomepage = 'no-trip-home';
    LocalStorageService.saveUser(this.userDetails).then(() => {
      console.log("User data saved to PouchDB");
      this.router.navigate([this.userDetails.currHomepage]);
    });
  }

}
